@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700&family=Noto+Sans+KR&display=swap');

.App {
  background-color: rgb(32, 33, 36);
}

* {
  color: #e8eaed;
  font-family: 'Nanum Gothic', sans-serif;
  box-sizing: border-box;
}

.loading {
  text-align: center;
  margin: 40px;
  font-size: 4rem;
  color: black;
}

.longtext {
  font-size: 0.8rem;
}

.longlongtext {
  font-size: 0.6rem;
}

.verygood {
  color: #CC4444;
}

.good {
  color: #4444CC;
}

.nb {
  color: #449944;
}

.bad {
  color: gray;
}

.sidebar {
  position: fixed;
  top: 50%;
  right: 20px;
  background-color: rgb(23, 23, 23);
  padding: 4px;
  border: 2px solid black;
  opacity: 0.4;
}

.send-nickname {
  padding-top: 20px;
  text-align: center;
}

.send-nickname input {
  width: 500px;
  height: 32px;
  padding: 4px;
  font-size: 0.8rem;
  border: 1px solid #5f6368;
  border-radius: 24px;
  background-color: rgb(32, 33, 36);
  outline: none;
  color: white;

}

.send-nickname input::placeholder {
  font-size: 0.8rem;
  font-weight: 300;
  padding: 16px;
}

.send-nickname button {
  background-color: rgb(48, 49, 52);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  color: #9aa0a6;
  height: 48px;
  margin-left: 10px;
  padding: 1rem;
  font-size: 0.8rem;
  border: 0;
  border-radius: 4px;
}

.wrapper,
.lol-rank {
  display: flex;
  justify-content: center;
}

.summoner-info {
  margin: 16px;
  width: 400px;
  padding: 16px;
  border: 2px solid black;
  text-align: center;
}

.lol-rank div {
  margin: 8px;
  text-align: center;
}

.tft-rank div div,
.tft-rank-flex div div {
  font-size: 1.52rem;
  font-weight: 500;
}

.lol,
.tft,
.summoner-info {
  border: 2px solid black;
  padding: 16px;
  background-color: rgb(23, 23, 23)
}

.match-wrap {
  display: flex;
  justify-content: center;
}

.wl {
  border-top: 1px solid white;
}

.win {
  color: blue;
}

.lose {
  color: red;
}

.title {
  text-align: center;
}

.match-wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.mastery-wrap {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.match-list {
  display: flex;
  flex-direction: column;
}

.match-list div h2 {
  text-align: center;
}

.match-item {
  background-color: rgb(23, 23, 23);
  border: 2px solid black;
  padding: 12px;
  display: flex;
  width: 800px;
  margin: 16px;

  font-size: 1rem;
}

.match-item div {
  margin: 6px;
}

.match-basic {
  width: 350px;
  text-align: center;
}

.match-info strong {
  color: lightblue;
}

.summoner-mastery {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.summoner-mastery-info {
  text-align: center;
  width: 250px;
  padding: 16px;
  background-color: rgb(23, 23, 23);
  margin: 4px;
  border: 2px solid black;
}

.summoner-mastery-info div {
  margin: 2px;
}